"use client";
import { useEffect, useState } from "react";
import { Pencil, Trash2 } from "lucide-react";
import { Button } from "../ui/button";

type Student = { id: number; name: string };
type Classroom = {
  id: number;
  name: string;
  year: string;
  students: Student[];
};

const MOCK_CLASSROOM: Classroom = {
  id: 1,
  name: "ថ្នាក់ 12 A",
  year: "2024-2025",
  students: [
    { id: 1, name: "ជោ" },
    { id: 2, name: "មេសា" },
    { id: 3, name: "សាខា" },
  ],
};

export default function ClassroomDetail({ classroomId }: { classroomId: string }) {
  const [classroom, setClassroom] = useState<Classroom | null>(null);

  useEffect(() => {
    // Use mock data for now
    setTimeout(() => {
      setClassroom({ ...MOCK_CLASSROOM, id: Number(classroomId) });
    }, 300); // Simulate loading
  }, [classroomId]);

  if (!classroom) return <div>Loading...</div>;

  return (
    <div className="p-6 md:p-10">
      <Button variant="ghost" className="mb-4">&larr; Go back</Button>
      <div className="bg-white rounded-xl p-6 shadow flex flex-col gap-4">
        <div className="flex items-center gap-4">
          <div className="w-12 h-12 rounded-full bg-green-600 flex items-center justify-center text-white text-2xl font-bold">
            {classroom.name.charAt(0)}
          </div>
          <div>
            <div className="font-semibold text-lg">{classroom.name} ({classroom.year})</div>
            <div className="text-gray-500 text-sm">Students: {classroom.students.length}</div>
          </div>
        </div>
        <div className="flex justify-between items-center mt-4">
          <div className="font-semibold">សិស្សទាំងអស់</div>
          <Button className="bg-[#25388C] hover:bg-[#1e2e6d] flex items-center gap-2">
            <span>បន្ថែមសិស្សថ្មី</span>
          </Button>
        </div>
        <table className="w-full mt-2">
          <thead>
            <tr className="bg-gray-50">
              <th className="text-left px-4 py-2">ឈ្មោះ</th>
              <th className="text-left px-4 py-2">សកម្មភាព</th>
            </tr>
          </thead>
          <tbody>
            {classroom.students.map((student) => (
              <tr key={student.id} className="border-b">
                <td className="px-4 py-2 font-medium">{student.name}</td>
                <td className="px-4 py-2 flex gap-2">
                  <button className="text-blue-500 hover:text-blue-700">
                    <Pencil size={18} />
                  </button>
                  <button className="text-red-500 hover:text-red-700">
                    <Trash2 size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}